@import "../../node_modules/normalize.css/normalize.css";
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/rajdhaniregular.woff2") format("woff2"), url("../fonts/rajdhaniregular.woff") format("woff");
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/rajdhanimedium.woff2") format("woff2"), url("../fonts/rajdhanimedium.woff") format("woff");
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/rajdhanisemibold.woff2") format("woff2"), url("../fonts/rajdhanisemibold.woff") format("woff");
}
@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/rajdhanibold.woff2") format("woff2"), url("../fonts/rajdhanibold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 1280px) {
  .container {
    padding: 0 52px;
  }
}

a {
  display: inline-block;
  text-decoration: none;
}

.page {
  height: 100%;
  overflow-x: hidden;
}
@media (min-width: 1280px) {
  .page {
    height: 100vh;
    overflow-y: hidden;
  }
}

.page__body {
  font-family: "Rajdhani", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  margin: 0 auto;
  min-width: 320px;
}
@media (min-width: 1280px) {
  .page__body {
    display: grid;
    grid-template-columns: 44% 1fr;
    column-gap: 40px;
  }
}

@media (min-width: 1280px) {
  .page__main {
    height: 100vh;
    overflow-y: scroll;
  }
}

.promo {
  min-height: 600px;
  position: relative;
  margin-bottom: 9px;
}
@media (min-width: 1280px) {
  .promo {
    min-height: 100vh;
    width: 100%;
    margin: 0;
  }
}

.logo {
  position: absolute;
  box-sizing: border-box;
  padding-top: 24px;
  padding-left: 16px;
  top: 0;
  left: 0;
  z-index: 20;
}
@media (min-width: 1280px) {
  .logo {
    padding-top: 32px;
    padding-left: 52px;
  }
}

@media (min-width: 1280px) {
  .logo__img {
    width: 95px;
    height: 53px;
  }
}

.slider {
  margin: 0 auto;
  color: #ffffff;
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.slider__item {
  position: relative;
  visibility: hidden;
  grid-row-start: 1;
  grid-column-start: 1;
}
.slider__item--current {
  visibility: visible;
}

.slider__img-wrap {
  position: relative;
}
.slider__img-wrap::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.22) 0%, rgba(53, 41, 37, 0.5) 100%);
  mix-blend-mode: multiply;
}

.slider__img {
  background-color: #c9b8a8;
  display: block;
  height: 600px;
  width: 100%;
}
@media (min-width: 1280px) {
  .slider__img {
    min-height: 100vh;
  }
}

.slider__description-wrap {
  position: absolute;
  padding: 0 16px;
  bottom: 90px;
}
@media (min-width: 720px) {
  .slider__description-wrap {
    bottom: 83px;
  }
}
@media (min-width: 1280px) {
  .slider__description-wrap {
    bottom: 97px;
    margin-left: 36px;
  }
}

.slider__name {
  font-weight: 400;
  font-size: 48px;
  line-height: 48px;
  margin: 0;
}
@media (min-width: 720px) {
  .slider__name {
    font-size: 64px;
    line-height: 64px;
  }
}
@media (min-width: 1280px) {
  .slider__name {
    font-size: 72px;
    line-height: 72px;
  }
}

.slider__slogan {
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  opacity: 0.8;
}
@media (min-width: 720px) {
  .slider__slogan {
    font-size: 24px;
    line-height: 32px;
  }
}
@media (min-width: 1280px) {
  .slider__slogan {
    font-size: 28px;
  }
}

.slider__bottom {
  position: absolute;
  left: 11px;
  bottom: 23px;
  width: 235px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1280px) {
  .slider__bottom {
    bottom: 29px;
    margin-left: 36px;
  }
}

.slider__counter {
  display: flex;
  justify-content: space-between;
  width: 70px;
  font-size: 20px;
  line-height: 32px;
}

.slider__controls {
  width: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.slider__btn {
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 50%;
  padding: 0;
  cursor: pointer;
  outline: transparent;
  position: relative;
}
.slider__btn:hover, .slider__btn:focus {
  border-color: #ffffff;
}
.slider__btn:active {
  background-color: #ffffff;
}
.slider__btn:active svg {
  fill: #3d5876;
}
.slider__btn svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: #ffffff;
}
.slider__btn--next {
  transform: rotateY(180deg);
}

.slider__dash,
.slider__total {
  opacity: 0.5;
}

.collection {
  margin-bottom: 40px;
}
@media (min-width: 1280px) {
  .collection {
    margin-top: 56px;
    margin-bottom: 33px;
  }
}

.title {
  text-transform: uppercase;
  color: #8a94a6;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 8px;
}

@media (min-width: 1280px) {
  .description {
    width: 383px;
  }
}

.description__title {
  margin-bottom: 15px;
}
@media (min-width: 1280px) {
  .description__title {
    margin-bottom: 24px;
  }
}

.description__name {
  color: #192b3f;
  margin-top: 0;
  margin-bottom: 14px;
  font-size: 32px;
  line-height: 32px;
}
@media (min-width: 720px) {
  .description__name {
    font-size: 48px;
    line-height: 56px;
  }
}

.description__text {
  color: #8a94a6;
  margin-top: 0;
  margin-bottom: 17px;
  font-size: 18px;
  line-height: 28px;
  padding-right: 10px;
}
@media (min-width: 720px) {
  .description__text {
    font-size: 20px;
    line-height: 32px;
    padding-right: 50px;
    margin-bottom: 5px;
  }
}
@media (min-width: 1280px) {
  .description__text {
    padding-right: 0;
    margin-bottom: 8px;
  }
}

.characteristics {
  border-top: 1px solid #e1e6ed;
  border-bottom: 1px solid #e1e6ed;
  padding: 16px 0;
  margin-bottom: 28px;
}
@media (min-width: 720px) {
  .characteristics {
    padding-top: 24px;
    margin-bottom: 17px;
  }
}
@media (min-width: 1280px) {
  .characteristics {
    padding-top: 17px;
    margin-bottom: 26px;
  }
}

.characteristics__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.characteristics__legend {
  padding: 0;
  margin-bottom: 12px;
}

.characteristics__top {
  display: flex;
  flex-direction: column;
}
@media (min-width: 720px) {
  .characteristics__top {
    flex-direction: row;
    margin-bottom: 12px;
  }
}
@media (min-width: 1280px) {
  .characteristics__top {
    margin-bottom: 2px;
  }
}

.characteristics__bottom {
  display: flex;
}

.colour {
  margin-bottom: 29px;
}
@media (min-width: 720px) {
  .colour {
    min-width: 148px;
    margin-right: 72px;
  }
}
@media (min-width: 1280px) {
  .colour {
    margin-right: 13px;
  }
}

.colour__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  margin-left: 4px;
}

.colour__item {
  width: 40px;
  height: 40px;
}
@media (min-width: 1280px) {
  .colour__item {
    width: 26px;
    height: 26px;
  }
}

.colour__mark {
  position: relative;
  display: block;
}
.colour__mark::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
@media (min-width: 1280px) {
  .colour__mark::before {
    width: 26px;
    height: 26px;
  }
}
.colour__mark--dark-blue::before {
  background-color: #192b3f;
}
.colour__mark--light-blue::before {
  background-color: #3d5876;
}
.colour__mark--bluesh-gray::before {
  background-color: #8a94a6;
}

.colour__input:checked + .colour__mark::before {
  box-shadow: 0 0 0 4px #ffffff, 0 0 0 5px #192b3f;
}

.colour__input:hover + .colour__mark::before,
.colour__input:focus + .colour__mark::before {
  box-shadow: 0 0 0 4px #ffffff, 0 0 0 5px #8a94a6;
}

.colour__input:active + .colour__mark::before {
  box-shadow: 0 0 0 4px #ffffff, 0 0 0 5px #192b3f;
}

.colour__input:disabled + .colour__mark::before {
  background-color: #e1e6ed;
  opacity: 0.5;
  pointer-events: none;
}

.size {
  margin-bottom: 32px;
}
@media (min-width: 720px) {
  .size {
    display: flex;
    align-items: center;
  }
}

@media (min-width: 720px) {
  .size__title {
    margin-bottom: 0;
  }
}
@media (min-width: 1280px) {
  .size__title {
    margin-bottom: 10px;
  }
}

.size__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  margin-bottom: 37px;
}
@media (min-width: 720px) {
  .size__list {
    margin-bottom: 0;
    margin-right: 30px;
  }
}
@media (min-width: 1280px) {
  .size__list {
    margin-right: 17px;
  }
}

.size__item {
  width: 40px;
}
.size__item:first-child {
  margin-left: -10px;
  margin-right: 10px;
}
@media (min-width: 1280px) {
  .size__item {
    width: 30px;
  }
  .size__item:first-child {
    margin: 0;
  }
}

.size__label {
  display: block;
  color: #8a94a6;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  position: relative;
  text-align: center;
}
@media (min-width: 1280px) {
  .size__label {
    font-size: 16px;
    line-height: 24px;
  }
}

.size__input:checked + .size__label {
  font-size: 28px;
  color: #192b3f;
  border-color: #192b3f;
}
.size__input:checked + .size__label::after {
  position: absolute;
  content: "";
  width: 48px;
  height: 48px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #192b3f;
  border-radius: 50%;
}
@media (min-width: 1280px) {
  .size__input:checked + .size__label::after {
    width: 32px;
    height: 32px;
  }
}
@media (min-width: 1280px) {
  .size__input:checked + .size__label {
    font-size: 20px;
  }
}

.size__input:hover + .size__label,
.size__input:focus + .size__label {
  font-size: 28px;
}
.size__input:hover + .size__label::after,
.size__input:focus + .size__label::after {
  border-color: #8a94a6;
}

.size__input:active + .size__label {
  font-size: 28px;
  color: #192b3f;
}
.size__input:active + .size__label::after {
  border-color: #192b3f;
}

.size__input:disabled + .size__label {
  color: #e1e6ed;
  opacity: 0.5;
  pointer-events: none;
}

.size__link {
  font-size: 16px;
  line-height: 24px;
  color: #c9b8a8;
  padding: 1px 12px;
  border: 1px solid #c9b8a8;
  border-radius: 24px;
}
.size__link:hover, .size__link:focus {
  opacity: 0.5;
}
.size__link:active {
  opacity: 1;
}

.quantity {
  margin-right: 74px;
}
@media (min-width: 720px) {
  .quantity {
    min-width: 148px;
    margin-right: 72px;
  }
}
@media (min-width: 1280px) {
  .quantity {
    margin-right: 13px;
  }
}

.quantity__counter {
  width: 78px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quantity__number {
  color: #8a94a6;
  font-size: 24px;
  line-height: 24px;
  width: 40px;
  text-align: center;
  border: none;
  pointer-events: none;
  background-color: transparent;
  padding: 0;
}

.quantity__btn {
  width: 16px;
  height: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
}
.quantity__btn:hover, .quantity__btn:focus {
  opacity: 0.5;
}
.quantity__btn:active {
  opacity: 1;
}
.quantity__btn--minus {
  background-image: url("../img/icons/minus.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.quantity__btn--plus {
  background-image: url("../img/icons/plus.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.quantity__btn--circle::after {
  position: absolute;
  content: "";
  width: 28px;
  height: 28px;
  top: 50%;
  left: 50%;
  border: 1px solid #e1e6ed;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.cost {
  font-size: 40px;
  line-height: 40px;
  color: #c9b8a8;
}

.cost__title {
  margin-bottom: 6px;
}

.cost__cents {
  font-size: 28px;
  line-height: 40px;
  color: #8a94a6;
}

.details {
  font-size: 16px;
  line-height: 24px;
  color: #192b3f;
  margin-bottom: 40px;
}
@media (min-width: 1280px) {
  .details {
    margin-bottom: 71px;
  }
}

.details__text {
  margin: 0;
  margin-bottom: 8px;
}

.details__composition {
  line-height: 20px;
  color: #8a94a6;
  margin: 0;
}

.services {
  margin-bottom: 40px;
}

.services__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.services__item {
  padding-bottom: 7px;
  border-bottom: 1px solid #e1e6ed;
  margin-bottom: 16px;
}

.services__accordion {
  display: block;
  position: relative;
  width: 100%;
  text-align: left;
  font-family: "Rajdhani", "Arial", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #192b3f;
  background-color: transparent;
  padding: 0;
  border: 0;
}
.services__accordion::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  background-image: url("../img/icons/plus.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.services__accordion--open::after {
  background-image: url("../img/icons/minus.svg");
}
.services__accordion:hover, .services__accordion:focus {
  opacity: 0.8;
}
.services__accordion:active {
  opacity: 1;
}

.services__description {
  font-size: 16px;
  line-height: 24px;
  color: #8a94a6;
}

.services__price {
  font-weight: 600;
}

.collections {
  margin-bottom: 52px;
}
@media (min-width: 720px) {
  .collections {
    margin-bottom: 48px;
  }
}
@media (min-width: 1280px) {
  .collections {
    margin-bottom: 36px;
  }
}

.collections__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1280px) {
  .collections__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
  }
}

.collections__item:not(:last-child) {
  margin-bottom: 16px;
}
@media (min-width: 720px) {
  .collections__item:not(:last-child) {
    margin-bottom: 28px;
  }
}

.collections__link {
  display: block;
  position: relative;
  box-sizing: border-box;
  color: #ffffff;
  border-radius: 4px;
  overflow: hidden;
  width: 100%;
}

.collections__link:focus,
.collections__link:hover {
  opacity: 0.95;
}

.collections__img-wrap {
  position: relative;
}
.collections__img-wrap::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.22) 0%, rgba(53, 41, 37, 0.22) 100%);
  mix-blend-mode: multiply;
}

.collections__img {
  display: block;
  width: 100%;
  height: 160px;
  background-color: #c4c4c4;
}
@media (min-width: 720px) and (max-width: 1199px) {
  .collections__img {
    height: 287px;
  }
}

.collections__description {
  position: absolute;
  bottom: 12px;
  padding: 0 16px;
}
@media (min-width: 720px) and (max-width: 1280px) {
  .collections__description {
    bottom: 17px;
  }
}

.collections__name {
  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  margin: 0;
}
@media (min-width: 720px) and (max-width: 1199px) {
  .collections__name {
    font-size: 40px;
    line-height: 40px;
  }
}

.collections__slogan {
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.5;
  margin: 0;
}
@media (min-width: 720px) and (max-width: 1199px) {
  .collections__slogan {
    font-size: 18px;
    line-height: 18px;
  }
}

.subscription {
  padding-bottom: 16px;
}

.subscription__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
  color: #192b3f;
  margin: 0;
  margin-bottom: 8px;
}
@media (min-width: 720px) {
  .subscription__title {
    font-size: 32px;
    line-height: 40px;
    margin-bottom: 0;
  }
}

.subscription__description {
  font-size: 18px;
  line-height: 28px;
  color: #8a94a6;
  opacity: 0.8;
  margin: 0;
  margin-bottom: 28px;
  padding-right: 20px;
}
@media (min-width: 720px) {
  .subscription__description {
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 24px;
  }
}

.subscription__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.subscription__gender-wrap {
  margin-bottom: 16px;
}
@media (min-width: 720px) {
  .subscription__gender-wrap {
    display: flex;
    margin-bottom: 40px;
    align-items: center;
  }
  .subscription__gender-wrap .subscription__item {
    margin-bottom: 0;
  }
  .subscription__gender-wrap .subscription__item:last-child {
    margin-bottom: 0;
  }
  .subscription__gender-wrap .subscription__legend {
    margin-bottom: 0;
    margin-right: 25px;
  }
}

.subscription__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.subscription__list--radio {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 24px;
}

.subscription__item {
  margin-bottom: 16px;
}
.subscription__item:last-child {
  margin-bottom: 12px;
}

.subscription__input {
  box-sizing: border-box;
  background-color: rgba(225, 230, 237, 0.2);
  border-color: transparent;
  border-radius: 1px;
  width: 100%;
  height: 44px;
  padding-left: 14px;
  opacity: 1;
  font-family: "Rajdhani", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
}
@media (min-width: 720px) {
  .subscription__input {
    width: 456px;
  }
}
.subscription__input::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 40px;
  color: #8a94a6;
}
.subscription__input:focus, .subscription__input:hover {
  box-shadow: 0 0 5px 0 #2e4055 inset;
}
.subscription__input:focus:invalid {
  box-shadow: 0 0 5px 0 #ff0000ba inset;
}

.subscription__legend {
  font-size: 20px;
  line-height: 32px;
  color: #8a94a6;
  opacity: 0.8;
  margin-bottom: 4px;
  padding: 0;
}

.subscription__label {
  font-size: 16px;
  line-height: 24px;
  color: #192b3f;
}

.subscription__label--radio {
  display: block;
  position: relative;
  padding-left: 28px;
}
.subscription__label--radio::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  width: 20px;
  height: 20px;
