.description {
  @media (min-width: $desktop-width) {
    width: 383px;
  }
}

.description__title {
  margin-bottom: 15px;

  @media (min-width: $desktop-width) {
    margin-bottom: 24px;
  }
}

.description__name {
  color: $dark-blue;
  margin-top: 0;
  margin-bottom: 14px;
  font-size: 32px;
  line-height: 32px;

  @media (min-width: $tablet-width) {
    font-size: 48px;
    line-height: 56px;
  }
}

.description__text {
  color: $bluesh-gray;
  margin-top: 0;
  margin-bottom: 17px;
  font-size: 18px;
  line-height: 28px;
  padding-right: 10px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 32px;
    padding-right: 50px;
    margin-bottom: 5px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 0;
    margin-bottom: 8px;
  }
}
