.colour {
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    min-width: 148px;
    margin-right: 72px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 13px;
  }
}

.colour__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  margin-left: 4px;
}

.colour__item {
  width: 40px;
  height: 40px;

  @media (min-width: $desktop-width) {
    width: 26px;
    height: 26px;
  }
}

.colour__mark {
  position: relative;
  display: block;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;

    @media (min-width: $desktop-width) {
      width: 26px;
      height: 26px;
    }
  }

  &--dark-blue {
    &::before {
      background-color: $dark-blue;
    }
  }

  &--light-blue {
    &::before {
      background-color: $light-blue;
    }
  }

  &--bluesh-gray {
    &::before {
      background-color: $bluesh-gray;
    }
  }
}

.colour__input:checked + .colour__mark::before {
  box-shadow: 0 0 0 4px $white, 0 0 0 5px $dark-blue;
}

.colour__input:hover + .colour__mark::before,
.colour__input:focus + .colour__mark::before {
  box-shadow: 0 0 0 4px $white, 0 0 0 5px $bluesh-gray;
}

.colour__input:active + .colour__mark::before {
  box-shadow: 0 0 0 4px $white, 0 0 0 5px $dark-blue;
}

.colour__input:disabled + .colour__mark::before {
  background-color: $light-gray;
  opacity: 0.5;
  pointer-events: none;
}
