.size {
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;
  }
}

.size__title {
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 10px;
  }
}

.size__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 7px;
  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    margin-right: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 17px;
  }
}

.size__item {
  width: 40px;

  &:first-child {
    margin-left: -10px;
    margin-right: 10px;
  }

  @media (min-width: $desktop-width) {
    width: 30px;

    &:first-child {
      margin: 0;
    }
  }
}

.size__label {
  display: block;
  color: $bluesh-gray;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  position: relative;
  text-align: center;

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.size__input:checked + .size__label {
  &::after {
    position: absolute;
    content: "";
    width: 48px;
    height: 48px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid  $dark-blue;
    border-radius: 50%;

    @media (min-width: $desktop-width) {
      width: 32px;
      height: 32px;
    }
  }

  font-size: 28px;
  color: $dark-blue;
  border-color: $dark-blue;

  @media (min-width: $desktop-width) {
    font-size: 20px;
  }
}

.size__input:hover + .size__label,
.size__input:focus + .size__label {
  font-size: 28px;

  &::after {
    border-color: $bluesh-gray;
  }
}

.size__input:active + .size__label {
  font-size: 28px;
  color: $dark-blue;

  &::after {
    border-color: $dark-blue;
  }
}

.size__input:disabled + .size__label {
  color: $light-gray;
  opacity: 0.5;
  pointer-events: none;
}

.size__link {
  font-size: 16px;
  line-height: 24px;
  color: $sandy;
  padding: 1px 12px;
  border: 1px solid $sandy;
  border-radius: 24px;

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  &:active {
    opacity: 1;
  }
}
